import * as React from 'react';

type TModalHistoryContext = {
  isOpenModalHistory: boolean;
  setIsOpenModalHistory: React.Dispatch<React.SetStateAction<boolean>>;
};

export const ModalHistoryContext = React.createContext<TModalHistoryContext | undefined>(undefined);

export const useModalHistoryContext = () => {
  const context = React.useContext(ModalHistoryContext);
  if (context === undefined) {
    throw new Error('useModalHistoryContext must be used within a ModalHistoryProvider');
  }
  return context;
};

type TActiveTabProviderProps = {
  children: React.ReactNode;
};

export const ModalHistoryProvider = ({ children }: TActiveTabProviderProps) => {
  const [isOpenModalHistory, setIsOpenModalHistory] = React.useState(false);

  return <ModalHistoryContext.Provider value={{ isOpenModalHistory, setIsOpenModalHistory }}>{children}</ModalHistoryContext.Provider>;
};
