import * as React from 'react';
import api from '../../../../../../../utils/axios';
import Select from 'react-select';
import { clsx } from 'clsx';
import { useValidationContext } from '../../../context/validationContext';
import { changeValueScreeningDocument } from '../../../context/action/validationAction';
import { useOptionsContext } from '../../../context/optionsContext';
import { toast } from 'react-toastify';
import { useActiveTabContext } from '../../../context/activeTabContext';

type TLastValidationComponentValue = {
  valueOptions: any;
  getDataVerDocList: (payload?) => void;
  getDashboardMini: () => void;
};

export default function LastValidationComponentValue({ valueOptions, getDataVerDocList, getDashboardMini }: TLastValidationComponentValue) {
  const { state, dispatch } = useValidationContext();
  const { options } = useOptionsContext();
  const [activeTab, setActiveTab] = useActiveTabContext();

  const [isLoadingSubmit, setIsLoadingSubmit] = React.useState<boolean>(false);

  const handleChangeMultiSelect = (name: any, values: any) => {
    const getArrayValue = values.map(({ value }) => value);
    dispatch({ type: 'CHANGE_VALUE', payload: { name, value: JSON.stringify(getArrayValue) } });
  };

  const handleChangeRadio = (e) => {
    const { name, value } = e.target;
    if (value === 'qualified') {
      dispatch({ type: 'CHANGE_VALUE', payload: { name, value } });
      dispatch({ type: 'CHANGE_VALUE', payload: { name: 'final_notes_selected', value: JSON.stringify([]) } });
      dispatch({ type: 'CHANGE_VALUE', payload: { name: 'final_notes', value: '' } });
      return;
    }

    dispatch({ type: 'CHANGE_VALUE', payload: { name, value } });
  };

  const screeningSaveDocument = async (e) => {
    e.preventDefault();
    setIsLoadingSubmit(true);
    try {
      const payload = {
        ...state,
        category: activeTab === 0 ? 'draft' : activeTab === 1 ? 'qc' : 'done',
      };

      const newPayload = Object.keys(payload).reduce((acc, key) => {
        if (!/^ocr_/.test(key)) {
          acc[key] = payload[key];
        }
        return acc;
      }, {});

      if (
        (!newPayload['final_notes'] ||
          !newPayload['final_notes_selected'] ||
          newPayload['final_notes_selected']?.length === 0 ||
          !newPayload['final_status']) &&
        newPayload['final_status'] !== 'qualified'
      ) {
        return toast.error('Cek kembali inputan tidak boleh kosong');
      }

      setIsLoadingSubmit(true);
      const response = await api.post('/screening/document/save', newPayload);
      if (response.status === 201) {
        getDataVerDocList(payload);
        getDashboardMini();
        toast.success(response.data.message);
        setIsLoadingSubmit(false);
      }
    } catch (error) {
      setIsLoadingSubmit(false);
      console.error(error);
      toast.error(error.response.data.message);
    }
  };

  if (!state) return;

  return (
    <form onSubmit={screeningSaveDocument}>
      <div className="border-b last:border-b-0 pb-4 min-h-[500px]">
        <div className={clsx('rounded-md m-2 text-sm space-y-1.5 p-2 ', state[`final_status`] === 'qualified' ? 'bg-[#E8FFE2]' : 'bg-[#FFE2E2]')}>
          <h4 className="font-bold">
            Validasi Akhir <span className="text-red-500 text-xs">*</span>
          </h4>
          <p className="text-gray-500 font-light">{`(Validasi dari keseluruhan data)`}</p>
          <hr className="border-[0.5px] border-gray-500" />

          <div className="space-x-1.5 w-full flex items-center">
            <input
              type="radio"
              id={`final_status_qualified`}
              value="qualified"
              name={`final_status`}
              onChange={handleChangeRadio}
              checked={state[`final_status`] === 'qualified'}
              required
            />
            <label htmlFor={`final_status_qualified`}>
              <span>Lolos</span>
            </label>
            <input
              type="radio"
              id={`final_status_disqualified`}
              value="disqualified"
              name={`final_status`}
              onChange={handleChangeRadio}
              checked={state[`final_status`] === 'disqualified'}
              required
            />
            <label htmlFor={`final_status_disqualified`}>
              <span>Gugur</span>
            </label>
          </div>
          <div className="space-y-2">
            <Select
              isDisabled={state[`final_status`] === 'qualified'}
              name={'final_notes_selected'}
              onChange={(value, name) => handleChangeMultiSelect(name.name, value)}
              isMulti
              closeMenuOnSelect={false}
              className="w-full bg-white rounded text-sm "
              options={options}
              value={valueOptions}
              required
            />
            <div>
              <label htmlFor="text-area-final-notes">Notes</label>
              <textarea
                disabled={state[`final_status`] === 'qualified'}
                onChange={changeValueScreeningDocument(dispatch)}
                name="final_notes"
                value={state['final_notes']}
                id="text-area-final-notes"
                className="rounded border p-2 inline-block w-full mt-2 min-h-44 disabled:bg-gray-100"
                required
              />
            </div>
          </div>

          <button disabled={isLoadingSubmit} type="submit" className="px-3 py-2 rounded-lg bg-blue-700 text-white text-xs">
            Save Verdoc
          </button>
        </div>
      </div>
    </form>
  );
}
