import * as React from 'react';
import { TValidationProviderProps, TValidationContext } from './context.types';
import { initialValueValidation, reducerValidation } from './action/validationAction';

export const ValidationContext = React.createContext<TValidationContext | undefined>(undefined);

export const useValidationContext = () => {
  const context = React.useContext(ValidationContext);
  if (context === undefined) {
    throw new Error('useValidationContext must be used within a ValidationProvider');
  }
  return context;
};

export const ValidationProvider = ({ children }: TValidationProviderProps) => {
  const [state, dispatch] = React.useReducer(reducerValidation, initialValueValidation);

  return <ValidationContext.Provider value={{ state, dispatch }}>{children}</ValidationContext.Provider>;
};
