export function capitalizeEachWord(str: string): string {
  if (str) {
    return str
      .split(' ') // Memisahkan string menjadi array kata
      .map((word) => word.charAt(0).toUpperCase() + word.slice(1).toLowerCase()) // Mengubah huruf pertama menjadi kapital dan sisanya menjadi huruf kecil
      .join(' '); // Menggabungkan kembali array menjadi string
  }

  return '';
}
