import { TValueValidation, ActionValidation } from '../context.types';

export const initialValueValidation: TValueValidation = null;

export const changeValueScreeningDocument =
  (dispatch: React.Dispatch<ActionValidation>) => (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    dispatch({ type: 'CHANGE_VALUE', payload: { name, value } });
  };

export const reducerValidation = (state: TValueValidation, action: ActionValidation) => {
  switch (action.type) {
    case 'MERGED_VALIDATION':
      return { ...state, ...action.payload };
    case 'CHANGE_VALUE':
      return { ...state, [action.payload.name]: action.payload.value };
    case 'RESET_VALIDATION':
      return { initialValueValidation };
    default:
      return state;
  }
};
