import * as React from 'react';
import { TScreeninTab } from './TScreeningTabs.types';
import { clsx } from 'clsx';
import TableScreening from '../table-screener';
import { useFieldContext } from '../../context/fieldsContext';
import sessions from '../../../../../../utils/sessions';
import { useActiveTabContext } from '../../context/activeTabContext';

export default function ScreeningTabs({
  dataTable,
  getScreeningForm,
  getScreeningDetailDocument,
  getDataDetailApplicant,
  resetState,
  activeVerdocId,
  isLoadingDataTable,
  setIsLoadingDataTable,
  getDataVerDocList,
}: TScreeninTab) {
  const [activeTab, setActiveTab] = useActiveTabContext();

  const { state } = useFieldContext();
  const userLevel = JSON.parse(sessions.getSessionUser())?.userlevel === 'C6E897BE4C224CABBAE0AEB0E876E697'; // screener

  const handleChangeTab = (category, activeTabbing) => {
    if (activeTab === activeTabbing) return;

    resetState();
    const payload = {
      ...state,
      category,
    };
    setActiveTab(activeTabbing);
    getDataVerDocList(payload);
  };

  React.useEffect(() => {
    if (userLevel) {
      setActiveTab(0);
    } else {
      setActiveTab(1);
    }
  }, [userLevel]);

  return (
    <React.Fragment>
      <TabHeader userLevel={userLevel} activeTab={activeTab} handleChangeTab={handleChangeTab} />
      <TableScreening
        dataTable={dataTable}
        getScreeningForm={getScreeningForm}
        getScreeningDetailDocument={getScreeningDetailDocument}
        getDataDetailApplicant={getDataDetailApplicant}
        resetState={resetState}
        activeApplicantId={activeVerdocId}
        isLoadingDataTable={isLoadingDataTable}
        setIsLoadingDataTable={setIsLoadingDataTable}
        getDataVerDocList={getDataVerDocList}
      />
    </React.Fragment>
  );
}

function TabHeader({ userLevel, handleChangeTab, activeTab }) {
  return (
    <header className={clsx('w-full flex items-center text-xs space-x-2', !userLevel ? 'justify-around' : 'justify-start')}>
      <button
        onClick={() => handleChangeTab('draft', 0)}
        className={clsx('border-b-2 w-1/3 text-center py-1.5', activeTab === 0 ? 'border-blue-700 text-blue-700 font-semibold' : '')}
      >
        Screening
      </button>
      {!userLevel && (
        <React.Fragment>
          <button
            onClick={() => handleChangeTab('qc', 1)}
            className={clsx('border-b-2 w-1/3 text-center py-1.5', activeTab === 1 ? 'border-blue-700 text-blue-700 font-semibold' : '')}
          >
            QC
          </button>
          <button
            onClick={() => handleChangeTab('done', 2)}
            className={clsx('border-b-2 w-1/3 text-center py-1.5', activeTab === 2 ? 'border-blue-700 text-blue-700 font-semibold' : '')}
          >
            Done
          </button>
        </React.Fragment>
      )}
    </header>
  );
}
