import * as React from 'react';

export const ActiveTabContext = React.createContext<any>(0);

export const useActiveTabContext = () => {
  const context = React.useContext(ActiveTabContext);
  if (context === undefined) {
    throw new Error('useActiveTabContext must be used within a ActiveTabProvider');
  }
  return context;
};

type TActiveTabProviderProps = {
  children: React.ReactNode;
};

export const ActiveTabProvider = ({ children }: TActiveTabProviderProps) => {
  const [activeTab, setActiveTab] = React.useState(0);

  return <ActiveTabContext.Provider value={[activeTab, setActiveTab]}>{children}</ActiveTabContext.Provider>;
};
