import * as React from 'react';

type TDataHistoryVerdoc = {
  dataHistory: any;
  setDataHistory: React.Dispatch<React.SetStateAction<any>>;
};

export const DataHistoryVerdoc = React.createContext<TDataHistoryVerdoc | undefined>(undefined);

export const useDataHistoryVerdoc = () => {
  const context = React.useContext(DataHistoryVerdoc);
  if (context === undefined) {
    throw new Error('useDataHistoryVerdoc must be used within a DataHistoryProvider');
  }
  return context;
};

type TDataHistoryProviderProps = {
  children: React.ReactNode;
};

export const DataHistoryProvider = ({ children }: TDataHistoryProviderProps) => {
  const [dataHistory, setDataHistory] = React.useState(null);

  return <DataHistoryVerdoc.Provider value={{ dataHistory, setDataHistory }}>{children}</DataHistoryVerdoc.Provider>;
};
