import * as React from 'react';
import { Typography, Dialog, DialogHeader, DialogBody } from '@material-tailwind/react';
import { useModalHistoryContext } from '../../context/modalHistoryContext';
import { useDataHistoryVerdoc } from '../../context/dataHistoryVerdoc';
import { HiMiniXMark } from 'react-icons/hi2';
import { FcTimeline } from 'react-icons/fc';

function MemoizeModalHistory() {
  const { isOpenModalHistory, setIsOpenModalHistory } = useModalHistoryContext();
  const { dataHistory, setDataHistory } = useDataHistoryVerdoc();

  const handleClosedModal = () => {
    setIsOpenModalHistory(false);
    setDataHistory(null);
  };

  if (!dataHistory) return <div>No data history</div>;

  return (
    <Dialog className="rounded-xl focus:outline-none relative overflow-y-auto" size="md" open={isOpenModalHistory}>
      <div className="bg-white z-20 font-normal text-gray-950 max-h-[40rem] overflow-y-auto relative min-h-[1rem]">
        <DialogHeader className="text-center flex flex-row items-center justify-between px-5 py-4 bg-white sticky top-0 z-10 border-b">
          <Typography variant="small" className="text-slate-900 font-bold">
            Validasi <span className="uppercase">{dataHistory.doc_type}</span>
          </Typography>
          <button onClick={handleClosedModal} className="focus:outline-none">
            <HiMiniXMark className="size-5 rounded-full border border-black" />
          </button>
        </DialogHeader>

        <DialogBody className="space-y-2 p-0 py-3 px-5">
          {dataHistory.data.length > 0 ? (
            <React.Fragment>
              {dataHistory.data.map((item) => {
                return <CardItem key={item.field_name} item={item} />;
              })}
            </React.Fragment>
          ) : (
            <NoDataHistory />
          )}
        </DialogBody>
      </div>
    </Dialog>
  );
}

function CardItem({ item }) {
  return (
    <div className="px-4 py-3 rounded-lg border text-left font-normal">
      <Typography variant="h6" className="text-slate-900 text-sm">
        KTP {item.field_name}
      </Typography>
      <div className="mt-2 pl-5">
        {item.activities.map(({ level, updated_at }) => {
          return <CardSubItem level={level} updated_at={updated_at} />;
        })}
      </div>
    </div>
  );
}

function CardSubItem({ level, updated_at }) {
  return (
    <React.Fragment>
      <div className="relative pb-5 group">
        <div className="flex flex-col sm:flex-row items-start mb-1 group-last:before:hidden before:absolute before:left-2 sm:before:left-0 before:h-full before:px-px before:bg-gray-200 before:self-start before:-translate-x-1/2 before:translate-y-2 after:absolute after:left-2 sm:after:left-0 after:w-4 after:h-4 after:bg-blue-800 after:box-content after:rounded-full  after:-translate-x-1/2 after:translate-y-1.5">
          <div className="ml-5 space-y-1">
            <p className="text-sm font-bold text-slate-900">{level}</p>
            <p className="text-xs text-gray-600 pb-1">{updated_at}</p>
          </div>
        </div>
      </div>
      {/* last */}
      <div className="pt-2"></div>
    </React.Fragment>
  );
}

function NoDataHistory() {
  return (
    <div className="flex flex-col items-center justify-center min-h-52">
      <FcTimeline className="size-20" />
      <h4 className="font-semibold text-lg mt-4">No Data History</h4>
    </div>
  );
}

const ModalHistory = React.memo(MemoizeModalHistory);
export default ModalHistory;
