import * as React from 'react';
import { useFieldContext } from '../context/fieldsContext';

export default function usePagination(dataTable: any, getDataVerDocList: (payload?) => void) {
  const { state, dispatch } = useFieldContext();

  const [currentPage, setCurrentPage] = React.useState<any>(1);
  const [rowsPerPage, setRowsPerPage] = React.useState<any>(10); // Default rows per page
  const [totalPage, setTotalPage] = React.useState<any>([]);

  // Hitung total halaman
  const totalLength = React.useMemo(() => Math.ceil(dataTable.data.length / rowsPerPage), [dataTable, rowsPerPage]);
  // const totalLength = Math.ceil(dataTable?.data.length / rowsPerPage);

  // Fungsi untuk mengatur data halaman
  const paginateData = () => {
    const startIndex = (currentPage - 1) * rowsPerPage;
    const endIndex = startIndex + rowsPerPage;
    return dataTable?.data.slice(startIndex, endIndex);
  };

  // Fungsi untuk mengatur list halaman
  const renderListPage = () => {
    const totalPageArray = Array.from({ length: totalLength }, (_, index) => index + 1);
    setTotalPage(totalPageArray);
  };

  // Efek samping untuk memanggil renderListPage saat komponen dimount atau totalLength berubah
  React.useEffect(() => {
    renderListPage();
  }, [dataTable, rowsPerPage]);

  // Handle perubahan halaman
  const handleChangePage = (pageNumber) => {
    setCurrentPage(pageNumber);
    dispatch({ type: 'CHANGE_PAGE', payload: { page: pageNumber } });

    const payload = {
      ...state,
      page: pageNumber,
    };

    getDataVerDocList(payload);
  };

  // Handle perubahan jumlah baris per halaman
  const handleChangeRowsPerPage = (e) => {
    setRowsPerPage(Number(e));
    dispatch({ type: 'CHANGE_SIZE', payload: { size: e } });

    const payload = {
      ...state,
      size: Number(e),
    };

    getDataVerDocList(payload);
  };

  // Tampilan pagination yang lebih kompleks
  const renderPagination = React.useCallback(() => {
    let pagination: any = [];
    const maxPages = 3; // Jumlah maksimum halaman yang ditampilkan di pagination

    if (totalPage.length <= maxPages) {
      pagination = totalPage;
    } else {
      const startPage = Math.max(1, currentPage - Math.floor(maxPages / 2));
      const endPage = Math.min(totalPage.length, startPage + maxPages - 1);

      if (startPage > 1) {
        pagination.push(1);
        if (startPage > 1) {
          pagination.push('...');
        }
      }

      for (let i = startPage; i <= endPage; i++) {
        pagination.push(i);
      }

      if (endPage < totalPage.length) {
        if (endPage < totalPage.length - 1) {
          pagination.push('...');
        }
        pagination.push(totalPage.length);
      }
    }

    return pagination;
  }, [totalPage]);

  return {
    handleChangePage,
    handleChangeRowsPerPage,
    renderPagination,
    paginateData,
    currentPage,
    rowsPerPage,
    totalPage,
  };
}
