import * as React from 'react';
import { FieldProvidersProps, TFieldsContext } from './context.types';
import { reducerField, initialValueField } from './action/fieldsAction';

export const FieldContext = React.createContext<TFieldsContext | undefined>(undefined);

export const useFieldContext = () => {
  const context = React.useContext(FieldContext);
  if (context === undefined) {
    throw new Error('useFieldCOntext must be used within a ResultProvider');
  }
  return context;
};

export const FieldProvider = ({ children }: FieldProvidersProps) => {
  const [state, dispatch] = React.useReducer(reducerField, initialValueField);

  return <FieldContext.Provider value={{ state, dispatch }}>{children}</FieldContext.Provider>;
};
