import * as React from 'react';
import { TOptionsContext } from './context.types';

export const OptionsContext = React.createContext<TOptionsContext | undefined>(undefined);

export const useOptionsContext = () => {
  const context = React.useContext(OptionsContext);
  if (context === undefined) {
    throw new Error('useOptionsContext must be used within a ValidationProvider');
  }
  return context;
};

type TOptionsProviderProps = {
  children: React.ReactNode;
};

export const OptionsProvider = ({ children }: TOptionsProviderProps) => {
  const [options, setOptions] = React.useState([]);

  return <OptionsContext.Provider value={{ options, setOptions }}>{children}</OptionsContext.Provider>;
};
