import * as React from 'react';
import api from '../../../../../../utils/axios';
import { TPreviewFile } from './TPriviewFile.types';
import { BsFiletypeJpg, BsFiletypePdf } from 'react-icons/bs';

export default function PreviewFile({ filename, user_applicant_id }: TPreviewFile) {
  const [fileSrc, setFileSrc] = React.useState(null);

  const getPreviewFile = async () => {
    try {
      const response = await api.post('/preview-file', {
        user_applicant_id,
        filename,
      });
      const result = response.data;
      setFileSrc(result);
    } catch (error) {
      console.error(error);
    }
  };

  const getFileExtension = (fileName: string) => {
    const parts = filename.split('.');
    return parts.length > 1 ? parts.pop() : '';
  };

  React.useEffect(() => {
    if (filename && user_applicant_id) {
      getPreviewFile();
    }
  }, [filename, user_applicant_id]);

  return (
    <div className="p-4 border-t">
      {fileSrc ? <embed src={fileSrc} type="application/pdf" width="100%" height="400px" /> : <div>Data tidak ditemukan</div>}

      {fileSrc && (
        <div className="-mx-4 border-t px-2 pt-2">
          <div className="flex justify-between items-center space-x-4">
            <div className="p-2 border rounded-md flex items-center space-x-2 flex-1">
              {getFileExtension(filename) === 'jpg' ? <JPGIcon /> : getFileExtension(filename) === 'pdf' ? <PDFIcon /> : ''}
              <p className="text-xs text-gray-600">{filename}</p>
            </div>

            <button className="px-4 py-2 rounded-md text-white bg-blue-700 text-xs hover:bg-blue-600">View</button>
          </div>
        </div>
      )}
    </div>
  );
}

