import * as React from 'react';
import PreviewFile from '../preview-file';
import { decode } from 'he';
import { Collapse } from '@material-tailwind/react';
import { clsx } from 'clsx';
import { ChecronDown, DocumentIcon } from '../../assets/icon';

export default function DetailApplicant({ detailApplicant }) {
  return (
    <main className="bg-white rounded-t-lg overflow-hidden h-full space-y-2">
      <div className="overflow-auto h-full p-4">
        <h1 className="text-center text-xl font-semibold">{detailApplicant.vacancy.title}</h1>
        <div className="wysiwyg max-w-prose" dangerouslySetInnerHTML={{ __html: decode(detailApplicant.vacancy.description) }} />
        <div className="p-3 rounded-lg bg-gray-50 border text-slate-600 drop-shadow-sm space-y-1">
          <h5 className="text-sm font-semibold text-slate-800">Major Qualification</h5>
          <p className="text-xs">{detailApplicant.vacancy.stream_name}</p>
        </div>
        <div className="space-y-3 pt-2">
          <h3 className="font-bold text-blue-700 text-lg">Pendidikan</h3>
          <hr className="border-[0.5px] border-gray-300" />
          <div>
            <table className="w-full text-left border-separate border-spacing-0 rounded-lg border overflow-hidden">
              <thead>
                <tr>
                  <th className="border-b border-blue-gray-100 bg-blue-gray-50 p-2 text-center">NIM</th>
                  <th className="border-b border-blue-gray-100 bg-blue-gray-50 p-2 text-center">Universitas</th>
                  <th className="border-b border-blue-gray-100 bg-blue-gray-50 p-2 text-center">Jurusan</th>
                  <th className="border-b border-blue-gray-100 bg-blue-gray-50 p-2 text-center">Jenjang Pendidikan</th>
                  <th className="border-b border-blue-gray-100 bg-blue-gray-50 p-2 text-center">IPK</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td className="text-xs text-slate-700 p-2 text-center">{detailApplicant.education['student_no']}</td>
                  <td className="text-xs text-slate-700 p-2 text-center">{detailApplicant.education['education_name']}</td>
                  <td className="text-xs text-slate-700 p-2 text-center">{detailApplicant.education['major_name']}</td>
                  <td className="text-xs text-slate-700 p-2 text-center">{detailApplicant.education['education_level']}</td>
                  <td className="text-xs text-slate-700 p-2 text-center">{detailApplicant.education['gpa']}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div className="space-y-3 pt-2">
          <h3 className="font-bold text-blue-700 text-lg">Dokumen Pendukung</h3>
          <hr className="border-[0.5px] border-gray-300" />
          {Object.entries(detailApplicant.other_docs).map(([key, value]: any) => {
            return (
              <DocumentCollapse key={key} header_name={key}>
                <PreviewFile user_applicant_id={detailApplicant.personal_info.user_id} filename={value} />
              </DocumentCollapse>
            );
          })}
        </div>
      </div>
    </main>
  );
}

function DocumentCollapse({ children, header_name }) {
  const [isOpenCollapse, setIsOpenCollapse] = React.useState(true);

  const handleOpenCollapse = () => {
    setIsOpenCollapse(!isOpenCollapse);
  };

  return (
    <div className={clsx('border rounded-lg')}>
      <HeaderCollapse handleOpenCollapse={handleOpenCollapse} header_name={header_name} />

      <Collapse className="h-full" open={isOpenCollapse}>
        {children}
      </Collapse>
    </div>
  );
}

function HeaderCollapse({ handleOpenCollapse, header_name }) {
  const removeUnderscores = (valueName: string) => {
    const result = valueName.replace(/_/g, ' ');
    return result;
  };

  return (
    <div className="cursor-pointer px-2 py-3" onClick={handleOpenCollapse}>
      <div className="flex justify-between items-center">
        <div className="space-x-3 flex items-center">
          <div>
            <DocumentIcon />
          </div>
          <div>
            <p className="uppercase tracking-widest flex-1">{removeUnderscores(header_name)}</p>
          </div>
        </div>
        <ChecronDown />
      </div>
    </div>
  );
}
