import * as React from 'react';
import { useFieldContext } from '../../context/fieldsContext';
import { capitalizeEachWord } from '../../utils/word';
import { clsx } from 'clsx';
import sessions from '../../../../../../utils/sessions';
import api from '../../../../../../utils/axios';
import { initialValueField } from '../../context/action/fieldsAction';

export default function Header({ listDashboardMini, getDataVerDocList }) {
  const { state, dispatch } = useFieldContext();
  const userLevel = JSON.parse(sessions.getSessionUser())?.userlevel === 'C6E897BE4C224CABBAE0AEB0E876E697';
  const [listScreener, setListScreener] = React.useState<any>([]);

  const handleChange = async (e) => {
    const { value } = e.target;
    const payload = {
      ...state,
      date_distribute: value,
    };

    dispatch({ type: 'CHANGE_DATE', payload: { date_distribute: value } });
    await getDataVerDocList(payload);
  };

  const resetFilter = async () => {
    dispatch({ type: 'RESET_FIELDS' });
  };

  const listChips = [
    {
      label: 'Total',
      value: 'total',
    },
    {
      label: 'Screening',
      value: 'total_draft',
    },
    {
      label: 'QC',
      value: 'total_qc',
    },
    {
      label: 'Done',
      value: 'total_done',
    },
    {
      label: 'Lulus',
      value: 'total_lulus',
    },
    {
      label: 'Gugur',
      value: 'total_gugur',
    },
  ];

  const getListScreener = async () => {
    try {
      const response = await api.post('/screening/list-screener', {});
      const result = response.data.data;
      setListScreener(result);
    } catch (error) {
      console.error(error);
    }
  };

  React.useEffect(() => {
    if (!userLevel) {
      getListScreener();
    }
  }, [userLevel]);

  const handleChangeSelectScreener = async (e) => {
    const { value } = e.target;

    const payload = {
      ...state,
      screener_user_id: value,
    };

    dispatch({ type: 'CHANGE_SCREENER', payload: { screener_user_id: value } });
    await getDataVerDocList(payload);
  };

  return (
    <header className="space-y-2">
      <div className="flex items-center justify-between">
        <div className="flex items-center space-x-2">
          {listChips.map(({ label, value }) => (
            <Chip key={value} variant={value}>
              <div className="text-xs">
                {capitalizeEachWord(label)} : <b>{listDashboardMini[value]}</b>
              </div>
            </Chip>
          ))}
        </div>
        <div className="flex items-center space-x-2">
          {!userLevel && (
            //   <Select
            //   value={state?.screener_user_id}
            //   onChange={(value) => handleChangeSelectScreener(value)}
            //   variant="outlined"
            //   name="screener"
            //   id="screener_select"
            //   className="!border !border-gray-100 focus:ring-2 focus:ring-offset-2 focus:ring-blue-700 px-2 py-1 text-xs bg-white rounded-md"
            //   labelProps={{ className: 'hidden' }}
            //   containerProps={{ className: '!w-[180px] !min-w-0' }}
            // >
            //   {listScreener.map((screener) => (
            //     <Option className="text-xs" value={screener.screener_user_id} key={screener.screener_user_id}>
            //       {screener.screener_name}
            //     </Option>
            //   ))}
            // </Select>

            <select
              value={state.screener_user_id}
              onChange={handleChangeSelectScreener}
              name="screener"
              id="screener_select"
              className="pl-2 pr-5 py-2 border border-gray-100 rounded focus:outline-none focus:ring-2 focus:ring-blue-700 focus:ring-offset-2 text-xs appearance-none w-36"
            >
              <option value="">Pilih Screener</option>
              {listScreener.map((screener) => (
                <option value={screener.screener_user_id} key={screener.screener_user_id}>
                  {screener.screener_name}
                </option>
              ))}
            </select>
          )}
          <input
            value={state.date_distribute}
            onChange={handleChange}
            name="date_distribute"
            type="date"
            className="px-5 py-2 border border-gray-100 rounded focus:outline-none focus:ring-2 focus:ring-blue-700 focus:ring-offset-2 text-xs"
          />
          <button onClick={resetFilter} className="px-4 py-2 rounded text-xs bg-blue-100 border-blue-600 text-slate-700 hover:bg-blue-50">
            Reset Filter
          </button>
        </div>
      </div>
      <hr />
    </header>
  );
}

type TChip = {
  children: React.ReactNode;
  variant: string;
};
function Chip({ children, variant }: TChip) {
  return (
    <div
      className={clsx(
        'px-3 py-1 rounded-full border',
        variant === 'total_lulus'
          ? 'bg-green-50 border-green-500 text-green-700'
          : variant === 'total_gugur'
          ? 'bg-red-50 border-red-500 text-red-700'
          : variant === 'total'
          ? 'bg-blue-50 border-blue-500 text-blue-700'
          : 'bg-white'
      )}
    >
      {children}
    </div>
  );
}
