import * as React from 'react';
import { TValidationCard } from './TvalidationCard.types';
import { clsx } from 'clsx';
import { ChecronDown, DocumentIcon } from '../../assets/icon';
import { Collapse } from '@material-tailwind/react';
import { useValidationContext } from '../../context/validationContext';
import LogHistoryComp from './log-history-component';

export default function ValidationCard({ validationData, children, activeVerdocId }: TValidationCard) {
  const [isOpenCollapse, setIsOpenCollapse] = React.useState(true);
  const { state } = useValidationContext();

  const childrenArray = React.Children.toArray(children);
  const isLastChild = childrenArray.length === 1;

  return (
    <div className={clsx('border rounded-lg')}>
      <div
        className="cursor-pointer px-2 py-3"
        onClick={() => {
          setIsOpenCollapse(!isOpenCollapse);
        }}
      >
        <div className="flex justify-between items-center">
          <div className="space-x-3 flex items-center">
            <div>
              <DocumentIcon />
            </div>
            <div>
              <p className="uppercase tracking-widest flex-1">validasi {validationData.doc_name}</p>
            </div>
          </div>
          <ChecronDown />
        </div>
      </div>

      <Collapse className="h-full" open={isOpenCollapse}>
        <LogHistoryComp doc_type={validationData.doc_type} activeVerdocId={activeVerdocId} />
        {children}
      </Collapse>
      {isOpenCollapse && !isLastChild && state[`ocr_${validationData.doc_type}_notes_summary`] && (
        <div className={clsx('p-2 rounded-b-lg space-y-2 border-t')}>
          <h4 className="font-bold after:content-['*'] after:text-red-500 after:ml-2">
            <span className="uppercase">{validationData.doc_name}</span> Notes
          </h4>
          <React.Fragment>
            {state[`ocr_${validationData.doc_type}_notes_summary`] &&
              JSON.parse(state[`ocr_${validationData.doc_type}_notes_summary`])?.map((item, index) => {
                return (
                  <div className="px-4 py-3 rounded-lg bg-orange-50 border-orange-500 text-gray-600 text-xs border">
                    <p key={index}>{item}</p>;
                  </div>
                );
              })}
          </React.Fragment>
        </div>
      )}
    </div>
  );
}
