import * as React from 'react';
import { FaClockRotateLeft } from 'react-icons/fa6';
import { useModalHistoryContext } from '../../../context/modalHistoryContext';
import { useDataHistoryVerdoc } from '../../../context/dataHistoryVerdoc';
import services from '../../../../../../../utils/services';

type TLogHistoryComp = {
  doc_type: string;
  activeVerdocId: string;
};

export default function LogHistoryComp({ doc_type, activeVerdocId }: TLogHistoryComp) {
  const { isOpenModalHistory, setIsOpenModalHistory } = useModalHistoryContext();
  const { dataHistory, setDataHistory } = useDataHistoryVerdoc();

  const getDataHistoryVerdoc = async () => {
    const payload = {
      file_code: doc_type,
      verdoc_id: activeVerdocId,
    };
    try {
      const response = await services.post('/screening/document/history', payload);
      const result = response.result;
      console.log('🐙 ~ getDataHistoryVerdoc ~ result:', result);
      setDataHistory({ data: result, doc_type });
    } catch (error) {
      console.error(error);
    }
  };

  const handleOpenModalHistory = async () => {
    await getDataHistoryVerdoc();
    setIsOpenModalHistory(true);
  };

  return (
    <div className="border-t flex items-center justify-between p-2">
      <h4 className="font-semibold">Log History</h4>
      <button
        disabled={isOpenModalHistory}
        onClick={handleOpenModalHistory}
        className="disabled:active:scale-100 disabled:hover:bg-white active:scale-90 duration-150 px-3 py-1 text-xs rounded-full font-light border border-slate-800 text-slate-900 shadow hover:bg-gray-50 flex items-center space-x-1"
      >
        <FaClockRotateLeft rotate={180} className="size-3 scale-x-[-1]" />
        <p>History</p>
      </button>
    </div>
  );
}
