import { ActionFields, TFields } from '../context.types';

export const initialValueField: TFields = {
  statusCode: 0,
  totalData: 0,
  search: '',
  page: 1,
  size: 10,
  sort: '',
  category: 'draft',
  date_distribute: '',
};

export const reducerField = (state: TFields, action: ActionFields) => {
  switch (action.type) {
    case 'MERGED_VALUE':
      return { ...state, ...action.payload };
    case 'CHANGE_DATE':
      return { ...state, date_distribute: action.payload.date_distribute };
    case 'CHANGE_SEARCH':
      return { ...state, search: action.payload.search };
    case 'CHANGE_PAGE':
      return { ...state, page: action.payload.page };
    case 'CHANGE_SIZE':
      return { ...state, size: action.payload.size };
    case 'CHANGE_SCREENER':
      return { ...state, screener_user_id: action.payload.screener_user_id };
    case 'RESET_FIELDS':
      return { ...state, ...initialValueField };
    default:
      return state;
  }
};
