import * as React from 'react';
import { clsx } from 'clsx';
import { useValidationContext } from '../../../context/validationContext';
import { BiSolidPencil } from 'react-icons/bi';
import { FaXmark } from 'react-icons/fa6';
import { GrPowerReset } from 'react-icons/gr';
import { Tooltip } from '@material-tailwind/react';

interface InputValueEdit {
  name: string;
  value: string;
}

const MemoizeValidationComponentValue = ({ validationValue, validationData, originalData }) => {
  const { state, dispatch } = useValidationContext();
  const [isOpenFieldEdit, setIsOpenFieldEdit] = React.useState<boolean>(false);
  const [currentValueEdit, setCurrentValueEdit] = React.useState<string>('');

  const handleChangeInputValueEdit = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    dispatch({ type: 'CHANGE_VALUE', payload: { name, value } });
  };

  const handleSaveInputValueEdit = () => {
    setIsOpenFieldEdit(false);
  };

  const handleCancelInputValueEdit = (validationData: any, validationValue: any) => {
    const { doc_type } = validationData;
    const { field_code } = validationValue;
    const currentValue = originalData[`scr_${doc_type}_${field_code}_value`];
    dispatch({ type: 'CHANGE_VALUE', payload: { name: `scr_${doc_type}_${field_code}_value`, value: currentValue } });
    setIsOpenFieldEdit(false);
  };

  const handleDeleteInputValueScr = (validationData: any, validationValue: any) => {
    const { doc_type } = validationData;
    const { field_code } = validationValue;
    dispatch({ type: 'CHANGE_VALUE', payload: { name: `scr_${doc_type}_${field_code}_value`, value: null } });
    setIsOpenFieldEdit(false);
  };

  const handleChangeValueRadio = (e: React.ChangeEvent<HTMLInputElement>, validationData: any, validationValue: any) => {
    const { name, value } = e.target;
    dispatch({ type: 'CHANGE_VALUE', payload: { name, value } });
  };

  return (
    <React.Fragment>
      <div key={validationValue.id} className="border-b last:border-b-0 first:border-t">
        {state && (
          <div
            className={clsx(
              'rounded-md m-2 text-sm space-y-1.5 p-2 ',
              state[`scr_${validationData.doc_type}_${validationValue.field_code}_status`] === 'lolos'
                ? 'bg-[#E8FFE2]'
                : state[`scr_${validationData.doc_type}_${validationValue.field_code}_status`] === 'gugur'
                ? 'bg-[#FFE2E2]'
                : 'bg-orange-50'
            )}
          >
            <h4 className="font-bold">
              {validationValue.field_name} <span className="text-red-500 text-xs">*</span>
            </h4>
            <p className="text-gray-500 font-light">{`(Pastikan ${validationValue.field_name} sesuai)`}</p>
            <hr className="border-[0.5px] border-gray-500" />
            {/* {state['type_engine'] === 'ocr' && ( */}
            <div>
              <div className="text-gray-500 flex items-center space-x-2">
                <h5>
                  <b>OCR Value:</b> <span className="font-light">{state[`ocr_${validationData.doc_type}_${validationValue.field_code}_value`]}</span>
                </h5>
                {!state[`scr_${validationData.doc_type}_${validationValue.field_code}_value`] && (
                  <Tooltip content="Edit">
                    <button onClick={() => setIsOpenFieldEdit(true)} className="p-1 rounded-lg hover:bg-gray-50">
                      <BiSolidPencil className="size-4 text-gray-800" />
                    </button>
                  </Tooltip>
                )}
              </div>

              {state[`scr_${validationData.doc_type}_${validationValue.field_code}_value`] && (
                <div className="text-gray-500 flex items-center space-x-2 ">
                  <h5>
                    <b>SCR Value:</b>
                    <span className="font-light">{state[`scr_${validationData.doc_type}_${validationValue.field_code}_value`]}</span>
                  </h5>

                  {!isOpenFieldEdit && (
                    <React.Fragment>
                      <Tooltip content="Edit">
                        <button onClick={() => setIsOpenFieldEdit(true)} className="p-1 rounded-lg hover:bg-gray-50">
                          <BiSolidPencil className="size-4 text-gray-800" />
                        </button>
                      </Tooltip>

                      <Tooltip content="Delete">
                        <button
                          onClick={() => handleDeleteInputValueScr(validationData, validationValue)}
                          className="p-1 rounded-lg hover:bg-gray-50"
                        >
                          <FaXmark className="size-4 text-gray-800" />
                        </button>
                      </Tooltip>

                      <Tooltip content="Reset">
                        <button
                          onClick={() => handleCancelInputValueEdit(validationData, validationValue)}
                          className="p-1 rounded-lg hover:bg-gray-50"
                        >
                          <GrPowerReset className="size-4 text-gray-800" />
                        </button>
                      </Tooltip>
                    </React.Fragment>
                  )}
                </div>
              )}
            </div>
            {/* // )} */}

            {state[`ocr_${validationData.doc_type}_${validationValue.field_code}_notes`] && (
              <div className="text-gray-500">
                <h5>
                  <b>Alasan:</b> <span className="font-light"> {state[`ocr_${validationData.doc_type}_${validationValue.field_code}_notes`]}</span>
                </h5>
              </div>
            )}

            <div className="space-x-1.5 w-full flex items-center">
              <input
                type="radio"
                id={`scr_${validationData.doc_type}_${validationValue.field_code}_sesuai`}
                value="lolos"
                name={`scr_${validationData.doc_type}_${validationValue.field_code}_status`}
                onChange={(e) => handleChangeValueRadio(e, validationData, validationValue)}
                checked={state[`scr_${validationData.doc_type}_${validationValue.field_code}_status`] === 'lolos'}
              />
              <label htmlFor={`scr_${validationData.doc_type}_${validationValue.field_code}_sesuai`}>
                <span>Sesuai</span>
              </label>
              <input
                type="radio"
                id={`scr_${validationData.doc_type}_${validationValue.field_code}_tidak_sesuai`}
                value="gugur"
                name={`scr_${validationData.doc_type}_${validationValue.field_code}_status`}
                onChange={(e) => handleChangeValueRadio(e, validationData, validationValue)}
                checked={state[`scr_${validationData.doc_type}_${validationValue.field_code}_status`] === 'gugur'}
              />
              <label htmlFor={`scr_${validationData.doc_type}_${validationValue.field_code}_tidak_sesuai`}>
                <span>Tidak Sesuai</span>
              </label>
            </div>

            {isOpenFieldEdit && (
              <React.Fragment>
                <div className="flex items-center justify-start space-x-2">
                  <WarningIcon />
                  <p>
                    Masukkan perbaikan <i>value</i> pada field berikut:
                  </p>
                </div>
                <div className="flex items-center space-x-2">
                  <input
                    name={`scr_${validationData.doc_type}_${validationValue.field_code}_value`}
                    value={state[`scr_${validationData.doc_type}_${validationValue.field_code}_value`]}
                    onChange={handleChangeInputValueEdit}
                    type="text"
                    className="px-5 py-3 rounded-lg border text-sm flex-1"
                  />
                  <button onClick={handleSaveInputValueEdit} className="px-4 py-3 rounded-lg bg-blue-700 text-white text-xs">
                    Save
                  </button>
                  <button
                    onClick={() => handleCancelInputValueEdit(validationData, validationValue)}
                    className="px-4 py-3 rounded-lg bg-red-700 text-white text-xs"
                  >
                    Cancel
                  </button>
                </div>
              </React.Fragment>
            )}
          </div>
        )}
      </div>
    </React.Fragment>
  );
};

function WarningIcon() {
  return (
    <svg xmlns="http://www.w3.org/2000/svg" width="15" height="13" fill="none" viewBox="0 0 15 13">
      <path
        fill="#FF7A00"
        d="M7.05 12.7c-1.945 0-3.582-.091-4.857-.203a1.17 1.17 0 01-.976-1.612 40.38 40.38 0 013.077-5.958 41.592 41.592 0 012.292-3.305 1.17 1.17 0 011.856 0c.67.87 1.46 1.975 2.28 3.286a40.412 40.412 0 013.067 5.921 1.17 1.17 0 01-.978 1.614 65.95 65.95 0 01-5.761.257z"
      ></path>
      <path
        fill="#DA4A54"
        d="M7.79 1.196c-.23.06-.443.204-.6.43A42.27 42.27 0 005.086 4.96a42.357 42.357 0 00-2.823 6.011c-.251.667.096 1.403.7 1.587-.27-.02-.527-.04-.77-.061a1.17 1.17 0 01-.978-1.612 40.398 40.398 0 013.079-5.958 41.558 41.558 0 012.291-3.305 1.168 1.168 0 011.203-.426z"
      ></path>
      <path
        fill="#FF7A00"
        d="M12.945 6.974V6.89c0-.3.232-.547.53-.568l.918-.063a.569.569 0 01.608.568v.21c0 .33-.28.59-.608.568l-.918-.063a.569.569 0 01-.53-.568zM12.237 4.245l-.043-.074a.569.569 0 01.176-.756l.763-.513a.569.569 0 01.81.187l.105.183a.569.569 0 01-.242.795l-.826.404a.569.569 0 01-.743-.226zM10.26 2.235l-.074-.042a.569.569 0 01-.227-.743l.405-.826a.569.569 0 01.795-.243l.183.105a.569.569 0 01.187.81l-.513.764a.569.569 0 01-.757.175zM2.056 6.974V6.89c0-.3-.232-.547-.53-.568L.608 6.26A.569.569 0 000 6.827v.21c0 .33.28.59.608.568l.918-.063a.569.569 0 00.53-.568zM2.764 4.245l.042-.074a.569.569 0 00-.175-.756l-.763-.513a.569.569 0 00-.81.187l-.106.183a.569.569 0 00.243.795l.826.404a.569.569 0 00.743-.226zM4.74 2.235l.074-.042a.569.569 0 00.227-.743L4.636.624A.569.569 0 003.841.38l-.182.105a.569.569 0 00-.188.81l.513.764a.569.569 0 00.757.175z"
      ></path>
      <path
        fill="#fff"
        d="M7.174 4.274c.22-.022.438-.022.657 0 .277.028.489.26.5.537.032.91.032 1.82 0 2.73a.559.559 0 01-.5.537 3.272 3.272 0 01-.657 0 .559.559 0 01-.499-.538c-.033-.91-.033-1.819 0-2.729a.559.559 0 01.5-.537zM7.19 9.078c.209-.01.417-.01.625 0a.559.559 0 01.53.532c.013.28.013.56 0 .84a.558.558 0 01-.53.531 6.51 6.51 0 01-.625 0 .558.558 0 01-.53-.532 9.307 9.307 0 010-.84.559.559 0 01.53-.531z"
      ></path>
    </svg>
  );
}

const ValidationComponentValue = React.memo(MemoizeValidationComponentValue);
export default ValidationComponentValue;
