import * as React from 'react';
import { useValidationContext } from '../../context/validationContext';
import { useFieldContext } from '../../context/fieldsContext';
import api from '../../../../../../utils/axios';
import { toast } from 'react-toastify';
import sessions from '../../../../../../utils/sessions';

export default function FooterScreeningDocument({ getDataVerDocList, getDashboardMini, resetState }) {
  const { state: stateValidation } = useValidationContext();
  const { state } = useFieldContext();
  const userLevel = JSON.parse(sessions.getSessionUser())?.userlevel === 'C6E897BE4C224CABBAE0AEB0E876E697'; // user level screener

  const [isLoadingSubmit, setIsLoadingSubmit] = React.useState<boolean>(false);

  const screeningSaveDocument = async (category: string) => {
    try {
      const payload = {
        ...stateValidation,
        category,
      };

      const newPayload = Object.keys(payload).reduce((acc, key) => {
        if (!/^ocr_/.test(key)) {
          acc[key] = payload[key];
        }
        return acc;
      }, {});

      if (
        (!newPayload['final_notes'] ||
          !newPayload['final_notes_selected'] ||
          newPayload['final_notes_selected']?.length === 0 ||
          !newPayload['final_status']) &&
        newPayload['final_status'] !== 'qualified'
      ) {
        return toast.error('Cek kembali inputan tidak boleh kosong');
      }

      setIsLoadingSubmit(true);

      const response = await api.post('/screening/document/save', newPayload);
      if (response.status === 201) {
        if (category === 'qc') {
          resetState();
        }

        await getDataVerDocList(state);
        await getDashboardMini();
        toast.success(response.data.message);
        setIsLoadingSubmit(false);
      }
    } catch (error) {
      setIsLoadingSubmit(false);
      console.error(error);
      toast.error(error.response.data.message);
    }
  };

  return (
    <footer className="p-2 flex justify-center items-center bg-white border-t space-x-2">
      <button
        disabled={isLoadingSubmit}
        onClick={() => screeningSaveDocument('draft')}
        className="px-4 py-2 rounded-lg bg-white border border-blue-700 text-blue-700 text-xs disabled:text-gray-500 disabled:border-none disabled:bg-gray-100 disabled:cursor-not-allowed"
      >
        Save Draft
      </button>
      <button
        disabled={isLoadingSubmit}
        onClick={() => screeningSaveDocument(userLevel ? 'qc' : 'done')}
        className="px-4 py-2 rounded-lg bg-blue-700 border text-white text-xs disabled:border-none disabled:bg-gray-300 disabled:cursor-not-allowed disabled:text-gray-400"
      >
        Submit
      </button>
    </footer>
  );
}
